.wpte-product-container {

    [class^='wpte-product-dive'],
    [class*=' wpte-product-dive'] {
        overflow: hidden;
    }

    [class^='wpte-product-dive'] .wpte-product-hover-figure-caption,
    [class*=' wpte-product-dive'] .wpte-product-hover-figure-caption {
        opacity: 0;
        transition-delay: 0s;
    }

    [class^='wpte-product-dive']:hover .wpte-product-hover-image,
    [class*=' wpte-product-dive']:hover .wpte-product-hover-image {
        opacity: 0;
        transition-delay: 0s;
    }

    [class^='wpte-product-dive']:hover .wpte-product-hover-figure-caption,
    [class*=' wpte-product-dive']:hover .wpte-product-hover-figure-caption {
        opacity: 1;
        transition-delay: 0.175s;
    }

    .wpte-product-dive {
        .wpte-product-hover-figure-caption {
            transform: scale(5);
        }
        &:hover {
            .wpte-product-hover-image {
                transform: scale(5);
            }
            .wpte-product-hover-figure-caption {
                transform: scale(1);
            }
        }
    }

    .wpte-product-dive-cc {
        .wpte-product-hover-figure-caption {
            transform: rotate(-45deg) scale(5);
        }
        &:hover {
            .wpte-product-hover-image {
                transform: rotate(45deg) scale(5);
            }
            .wpte-product-hover-figure-caption {
                transform: rotate(0deg) scale(1);
            }
        }
    }
    
    .wpte-product-dive-ccc {
        .wpte-product-hover-figure-caption {
            transform: rotate(45deg) scale(5);
        }
        &:hover {
            .wpte-product-hover-image {
                transform: rotate(-45deg) scale(5);
            }
            .wpte-product-hover-figure-caption {
                transform: rotate(0deg) scale(1);
            }
        }
    }
}